////
/// Animations
/// @group components
////


@each $direction in $animation-directions {
    @keyframes fade-#{$direction} {
        from {
            @if ($direction == in) {
                opacity: 0;
            }
            @else {
                opacity: 1;
            }
        }
        to {
            @if ($direction == in) {
                opacity: 1;
            }
            @else {
                opacity: 0;
            }
        }
    }
}

@each $direction in $animation-directions {
    @each $axis in $animation-axes-directions {
        @keyframes rotate-#{$axis}-#{$direction} {
            from {
                @if ($direction == in) {
                    @if ($axis == horizontal) {
                        transform: rotateY(-90deg);
                    }
                    @else {
                        transform: rotateX(-90deg);
                    }
                }
                @else {
                    transform: none;
                }
            }
            to {
                @if ($direction == in) {
                    transform: none;
                }
                @else {
                    @if ($axis == horizontal) {
                        transform: rotateY(-90deg);
                    }
                    @else {
                        transform: rotateX(-90deg);
                    }
                }
            }
        }
    }
}

@each $direction in $animation-directions {
    @each $axis in $animation-axes-directions {
        @keyframes scale-#{$axis}-#{$direction} {
            from {
                @if ($direction == in) {
                    @if ($axis == horizontal) {
                        transform: scaleY(0);
                    }
                    @else {
                        transform: scaleX(0);
                    }
                }
                @else {
                    transform: none;
                }
            }
            to {
                @if ($direction == in) {
                    transform: none;
                }
                @else {
                    @if ($axis == horizontal) {
                        transform: scaleY(0);
                    }
                    @else {
                        transform: scaleX(0);
                    }
                }
            }
        }
    }
}

@each $direction in $animation-directions {
    @each $slide, $value in $animation-cardinal-directions {
        @keyframes slide-#{$slide}-#{$direction} {
            from {
                @if ($direction == in) {
                    @if ($slide == top or $slide == bottom) {
                        transform: translateY(#{$value});
                    }
                    @else {
                        transform: translateX(#{$value});
                    }
                }
                @else {
                    transform: none;
                }
            }
            to {
                @if ($direction == in) {
                    transform: none;
                }
                @else {
                    @if ($slide == top or $slide == bottom) {
                        transform: translateY(#{$value});
                    }
                    @else {
                        transform: translateX(#{$value});
                    }
                }
            }
        }
    }
}
